.columns {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	&--center {
		justify-content: center;
	}
	&--start {
		justify-content: flex-start;
	}
	&--align {
		align-items: center;
	}
	&__column {
		&--1 {
			width: calc(100% / 12 - 10px);
		}
		&--2 {
			width: calc(100% / 12 * 2 - 10px);
			@include r(1200) {
				width: calc(100% / 12 * 4 - 10px);
			}
			@include r(560) {
				width: calc(100% / 12 * 6 - 10px);
			}
			@include r(360) {
				width: 100%;
			}
		}
		&--3 {
			width: calc(100% / 12 * 3 - 10px);
			@include r(1200) {
				width: 100%;
			}
		}
		&--4 {
			width: calc(100% / 12 * 4 - 10px);
			@include r(1200) {
				width: 100%;
			}
		}
		&--5 {
			width: calc(100% / 12 * 5 - 10px);
			@include r(1200) {
				width: 100%;
			}
		}
		&--6 {
			width: calc(50% - 10px);
			@include r(1200) {
				width: 100%;
			}
		}
		&--7 {
			width: calc(100% / 12 * 7 - 10px);
			@include r(1200) {
				width: 100%;
			}
		}
		&--8 {
			width: calc(100% / 12 * 8 - 10px);
			@include r(1200) {
				width: 100%;
			}
		}
		&--9 {
			width: calc(100% / 12 * 9 - 10px);
			@include r(1200) {
				width: 100%;
			}
		}
		&--10 {
			width: calc(100% / 12 * 10 - 10px);
			@include r(1200) {
				width: 100%;
			}
		}
		&--12 {
			width: 100%;
		}
		&--centered {
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
		&--mob-order-1 {
			@include r(1200) {
				order: -1;
			}
		}
		&--mob-1 {

		}
		&--mob-2 {
			margin-top: rem(60);
			@include r(1200) {
				margin-top: 0;
			}	
		}
		&--mob-3 {
			img {
				margin-top: rem(-240);
				@include r(1200) {
					margin-top: 0;
				}
			}
		}
	}
}